import { HttpClient } from "@angular/common/http";
import { ChangeDetectorRef, Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Subject } from "rxjs";
import { AppConstants } from "src/app/shared/config/app.constant";
import { permissionsLabels } from "src/app/shared/models/fmosNewRolePermissions";
import { AddSystemService } from "src/app/shared/services/add-system.service";
import { ApiService } from "src/app/shared/services/api.service";
import { RoleService } from "src/app/shared/services/role.service";
import Swal from "sweetalert2";

@Component({
  selector: "npr-add-system-summary",
  templateUrl: "./add-system-summary.component.html",
  styleUrls: ["./add-system-summary.component.scss"],
})
export class AddSystemSummaryComponent implements OnInit {
  respArray: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  isLoading: boolean = false;
  roleCodes = new permissionsLabels();
  basicInfoForm: any;
  messageConfigurationForm: any;
  tcpForm: any;
  messageConfigurationForm1: any;
  tcpForm1: any;
  addSystem: any;
  currentUser: any;
  totalPages: any;
  totalRecords: any;
  respData: any;
  API_URL = AppConstants.baseURL;
  statusArr: String[] = ["Authorized", "Unauthorized", "Open", "Close"];

  constructor(
    private apiService: AddSystemService,
    private roleService: RoleService,
    private router: Router,
    private cdr: ChangeDetectorRef,
    private http: HttpClient
  ) {}

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: "full_numbers",
      pageLength: 5,
      serverSide: true,
      // processing: true,
      columnDefs: [{ type: "date", targets: [5] }],
      order: [[5, "desc"]],
      // lengthMenu: [[5, 10, 20, -1], [5, 10, 20, 30]],
      lengthMenu: [
        [5, 10, 20, 30, -1],
        [5, 10, 20, 30, "ALL"],
      ],
      ajax: (dataTablesParameters: any, callback) => {
        dataTablesParameters.minNumber = dataTablesParameters.start + 1;
        dataTablesParameters.maxNumber =
          dataTablesParameters.start + dataTablesParameters.length;
        console.log(
          dataTablesParameters.minNumber,
          dataTablesParameters.maxNumber
        );
        const pageNo = Math.floor(
          dataTablesParameters.minNumber / dataTablesParameters.length
        );
        const pageSize = dataTablesParameters.length;
        const search = dataTablesParameters.search.value;
        this.http
          .get<any>(
            `${this.API_URL}` +
              `/newExtApi/getAllExternalSys?pageNo=${pageNo}&pageSize=${pageSize}&search=${search}`
          )
          .subscribe((resp) => {
            console.log(resp);
            for (let data of resp.result) {
              data.approvedStatus = this.statusArr.find((i) =>
                i.startsWith(data.approvedStatus)
              );

              data.recordStatus = this.statusArr.find((i) =>
                i.startsWith(data.recordStatus)
              );
            }

            this.totalPages = resp.totalNoOfPages;
            this.totalRecords = resp.totalRecords;
            this.respArray = resp.result;
            this.isLoading = false;
            this.cdr.markForCheck();

            callback({
              recordsTotal: resp.totalRecords,
              recordsFiltered: resp.totalRecords,
              data: [],
            });
          });
      },
    };
    this.currentUser = localStorage.getItem("userFromLogin");
    // this.getDataForMonitor();
    this.roleService.fetchScreenPermissions("Add System");
    this.roleService.screenLabelList.subscribe(
      (message) => (this.roleCodes = message)
    );
    console.log(this.roleCodes);
  }
  getDataForMonitor() {
    this.isLoading = true;
    this.apiService.fecthingAddSystem().subscribe((dataresp) => {
      for (let data of dataresp.result) {
        data.approvedStatus = this.statusArr.find((i) =>
          i.startsWith(data.approvedStatus)
        );

        data.recordStatus = this.statusArr.find((i) =>
          i.startsWith(data.recordStatus)
        );
      }
      // this.respArray = dataresp.result;
      this.isLoading = false;
      this.cdr.markForCheck();

      this.dtTrigger.next();
    });
  }
  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
  close(data) {
    Swal.fire({
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: "You are trying to Close record. " + " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3e54b8",
      cancelButtonColor: "#d33",
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
      iconColor: "#d33",
    }).then((result) => {
      if (result.isConfirmed === true) {
        this.apiService
          .editaddsystem("close", this.currentUser, data)
          .subscribe((response) => {
            if (response) {
              this.respData = response;
              Swal.fire({ text: "Record is Closed", icon: "success" }).then(
                () => window.location.reload()
              );
            }
          });
      }
    });
  }

  open(data) {
    Swal.fire({
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: "You are trying to Reopen record. " + " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3e54b8",
      cancelButtonColor: "#d33",
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
      iconColor: "#d33",
    }).then((result) => {
      if (result.isConfirmed === true) {
        //submitAll
        this.apiService
          .editaddsystem("open", this.currentUser, data)
          .subscribe((response) => {
            if (response) {
              this.respData = response;
              this.auditLog();
              Swal.fire({ text: "Record is Reopened", icon: "success" }).then(
                () => window.location.reload()
              );
            }
          });
      }
    });
  }
  delete(data) {
    Swal.fire({
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: "You are trying to Delete record. " + " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3e54b8",
      cancelButtonColor: "#d33",
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
      iconColor: "#d33",
    }).then((result) => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.apiService
          .deleteAddSystem(this.currentUser, data.systemId)
          .subscribe((response) => {
            if (response) {
              Swal.fire({ text: "Record is Deleted", icon: "success" }).then(
                () => window.location.reload()
              );
            }
          });
      }
    });
  }
  auth(data) {
    Swal.fire({
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: "You are trying to Authorize record. " + " Do you want to proceed?",
      showCancelButton: true,
      confirmButtonColor: "#3e54b8",
      cancelButtonColor: "#d33",
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: "NO",
      confirmButtonText: "YES",
      icon: "info",
      iconColor: "#d33",
    }).then((result) => {
      if (result.isConfirmed === true) {
        if (this.currentUser === data.creatorId) {
          Swal.fire({ text: "Maker Cannot Authorize Record!", icon: "error" });
          return;
        }
        this.apiService
          .editaddsystem("auth", this.currentUser, data)
          .subscribe((response) => {
            if (response) {
              this.respData = response;
              this.auditLog();
              Swal.fire({ text: "Record is Authorized", icon: "success" }).then(
                () => window.location.reload()
              );
            }
          });
      }
    });
  }
  auditLog() {
    if (this.respData.approverId === "null") {
      this.respData.approverId = " ";
    }
    if (this.respData.approvedStatus === "N") {
      this.respData.approvedStatus = "UNAUTHORIZED";
    }
    if (this.respData.approvedStatus === "Y") {
      this.respData.approvedStatus = "AUTHORIZED";
    }
    if (this.respData.approvedStatus === "A") {
      this.respData.approvedStatus = "AUTHORIZED";
    }
    if (this.respData.approvedStatus === "U") {
      this.respData.approvedStatus = "UNAUTHORIZED";
    }
    if (this.respData.recordStatus === "O") {
      this.respData.recordStatus = "OPEN";
    }
    if (this.respData.recordStatus === "C") {
      this.respData.recordStatus = "CLOSED";
    }
    if (this.respData.approvedEver === "N") {
      this.respData.approvedEver = "NO";
    }
    if (this.respData.approvedEver === "Y") {
      this.respData.approvedEver = "YES";
    }
  }
  getResp(data) {
    localStorage.removeItem("idValue");
    console.log(data);
    this.apiService.setIndex({
      index: data.systemId,
    });
    localStorage.setItem("idValue", data.systemId);
    this.router.navigateByUrl("/addSystem");
  }
  new() {
    localStorage.removeItem("idValue");
    this.apiService.setIndex({
      index: "",
    });
    this.router.navigateByUrl("/addSystem");
  }
  refreshPage() {
    window.location.reload();
  }
}
